	:is(*, *::before, *::after) {
		-webkit-animation: none !important;
		        animation: none !important;
		transition: none !important;
	}
}

:root {
	--white-space: 20px;
	--color-app: #ffffff;
	--color-primary: #5263c6;
	--color-gray: #969696;
	--color-gray-dark: #2b2b2b;
}

@media (prefers-color-scheme: dark) {
	:root {
		--white-space: 20px;
		--color-app: #000000;
		--color-primary: #5263c6;
		--color-gray: #969696;
		--color-gray-dark: #c0c0c0;
	}
}

html {
	box-sizing: border-box;
	background: var(--color-app);
	scroll-behavior: smooth;
	-webkit-tap-highlight-color: transparent;
}

body {
	width: 100%;
	height: 100vh;
	background-color: transparent;
	font-size: 18px;
}

div[id='root'] {
	width: 100%;
	height: 100%;
	background-color: transparent;
}




header {
	position: relative;
	width: 100%;
	height: 250px;
	background: url(/static/media/pattern-bg.a60a3c85.png) no-repeat center center /
		cover;
	box-shadow: 0 20px 24px -12px rgba(0, 0, 0, 0.3);
	z-index: 405;
}

main {
	width: 100%;
	height: calc(100vh - 250px);
	overflow: hidden;
}

main > div {
	width: 100%;
	height: 100%;
}

